.App {
  position: relative;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* background-image: url('https://wallpapercave.com/wp/wp8984747.jpg'); */
  background-color: #f6d5e5;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.heading {
  margin-top: 10px;
  text-transform: uppercase;
  font-family: verdana;
  font-size: 5em;
  font-weight: 700;
  color: #f5f5f5;
  text-shadow: 1px 1px 1px #ffffff,
    1px 2px 1px #919191,
    1px 3px 1px #919191,
    1px 4px 1px #919191,
    1px 5px 1px #919191,
    1px 6px 1px #919191,
    1px 7px 1px #919191,
    1px 8px 1px #919191,
    1px 9px 1px #919191,
    1px 10px 1px #919191,
    1px 18px 6px rgba(16, 16, 16, 0.4),
    1px 22px 10px rgba(16, 16, 16, 0.2),
    1px 25px 35px rgba(16, 16, 16, 0.2),
    1px 30px 60px rgba(16, 16, 16, 0.4);
}

.game__area {
  margin: auto;
  display: grid;
  grid-template-columns: auto auto auto;
  grid-template-rows: auto auto auto;
  column-gap: 10px;
  row-gap: 10px;
}

svg {
  stroke-width: 15px;
  stroke: #74cf5c;
  fill: none;
  stroke-linecap: round;

}

#teamA {
  position: absolute;
  top: 50%;
  left: 10%;
  font: 74px;
  font-size: 40px;

}

#teamA span {
  color: #74cf5c;
}

#teamB {
  position: absolute;
  top: 49%;
  left: 75%;
  font: 74px;
  font-size: 40px;
}

#teamB span {
  color: #eabe55;
}