.result__screen {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .result__screen h1 {
    margin: 335px 0 50px 0;
    font-size: 3.5rem;
  }
  
  .result__screen button {
    border: 1px solid #000;
    border-radius: 5px;
    background-color: #000;
    font-weight: bold;
    color: #ffffff;
    padding: 10px 15px;
    cursor: pointer;
  }